html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Raleway", sans-serif;
}

img {
  max-width: 100%;
  display: block;
}

p {
  font-size: 1.6rem;
  margin: 0;
  padding: 0;
  line-height: 1.8;
  color: #000000;
}

a {
  text-decoration: none;
  color: #000000;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style: none;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Staatliches", cursive;
}

h2 {
  font-size: 4rem;
  font-family: "Staatliches", cursive;
}

h3 {
  font-size: 3rem;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

.img {
  width: 100%;
}

.section {
  padding: 8rem 0;
  overflow: hidden;
}

[class$=heading] {
  margin: 2rem 0;
}

.header {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
.nav__link {
  font-size: 1.4rem;
  display: flex;
  align-items: center;
}
.nav__link--active {
  font-weight: 700;
  color: #b8336a;
}
@media (min-width: 768px) {
  .nav__link--create::after {
    content: "";
    display: inline-block;
    height: 4rem;
    border-right: 0.2rem solid #f2f2f2;
    margin: 0 0 0 2rem;
  }
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
  }
}

.notwebp.notavif .hero {
  background-image: url(../img/page/hero.jpg);
}

.webp .hero {
  background-image: url(../img/page/hero.webp);
}

.avif .hero {
  background-image: url(../img/page/hero.avif);
}

.hero {
  min-height: 60rem;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.hero__text {
  max-width: 60rem;
  color: #ffffff;
}
.hero__link {
  font-size: 2rem;
  padding: 1.5rem 4rem;
  border: none;
  font-weight: 700;
  display: inline-block;
  margin-top: 2rem;
  transition: all 0.2s ease;
  cursor: pointer;
  border: none;
  color: #ffffff;
  background-color: #ff8015;
}
.hero__link:hover {
  background-color: #e16700;
}
.hero__link:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .hero__link {
    width: auto;
  }
}

.events {
  padding: 5rem 0;
}
.events__heading {
  text-align: center;
  margin: 0;
  color: #484349;
}
.events__grid {
  margin-top: 2rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .events__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .events__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.event {
  border: 0.1rem solid #f2f2f2;
}
.event__img {
  margin: 0 auto;
  width: 100%;
}
.event__content {
  padding: 2rem;
}
.event__date {
  font-size: 1.4rem;
}
.event__name {
  margin: 1rem 0;
  color: #484349;
}
.event__author {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.event__author-image {
  width: 10rem;
  overflow: hidden;
  border-radius: 50%;
}
.event__organization-text, .event__organization-name {
  margin: 0.5rem 0;
}
.event__organization-text {
  font-weight: 700;
}

.search {
  padding: 5rem 0;
  background-color: #f2f2f2;
}
.search__heading {
  text-align: center;
  margin: 0;
  color: #484349;
}

.form {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.form__field {
  padding: 1rem;
  border: 0.1rem solid #c7c3c8;
}
.form__submit {
  font-size: 2rem;
  padding: 1.5rem 4rem;
  border: none;
  font-weight: 700;
  display: inline-block;
  margin-top: 2rem;
  transition: all 0.2s ease;
  cursor: pointer;
  border: none;
  color: #ffffff;
  background-color: #b8336a;
  margin-top: 0;
}
.form__submit:hover {
  background-color: #902853;
}
.form__submit:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .form__submit {
    width: auto;
  }
}
@media (min-width: 768px) {
  .form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
    grid-template-rows: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .form {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: unset;
  }
}

.categories {
  padding: 5rem 0;
  background-color: #f2f2f2;
}
.categories__heading {
  text-align: center;
  margin: 0;
  color: #484349;
}
.categories__grid {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .categories__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .categories__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.category {
  transition: all 0.2s ease;
}
.category:hover {
  background-color: #e0dee0;
}
.category__name {
  font-size: 2.4rem;
  font-weight: 700;
  text-align: center;
}

.groups {
  padding: 5rem 0;
  color: #ffffff;
  background-image: linear-gradient(to bottom, #b8336a 0%, #b8336a 50%, #068d9d 50%, #068d9d 100%);
}
.groups__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .groups__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
.groups__heading {
  font-size: 3rem;
}
.groups__text {
  line-height: 2;
  font-size: 2.4rem;
  color: #ffffff;
}
@media (min-width: 768px) {
  .groups {
    background-image: linear-gradient(to right, #b8336a 0%, #b8336a 50%, #068d9d 50%, #068d9d 100%);
  }
}

.meeti__name {
  text-align: center;
  margin: 0;
  color: #484349;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .meeti__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.meeti__info {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .meeti__info {
    margin-bottom: 0;
    grid-column: 1/3;
  }
}

.sidebar__map {
  width: 100%;
}
.sidebar__content {
  padding: 2rem;
  text-align: center;
  background-color: #068d9d;
}
.sidebar__content > * {
  color: #ffffff;
}
.sidebar__label {
  font-weight: 700;
}

.footer {
  padding-top: 5rem;
}
.footer__copyright {
  padding: 1rem;
  text-align: center;
}

.mask {
  overflow: hidden;
  width: 100%;
}